import './SommairePanier.scss';

export default function SommairePanier(props){

    console.log("le contenue dans sommairePanier : ", props.panier);
    const info = calculInfoPanier(props.panier);

    return(
        <div className="SommairePanier">
            <span>Article differents : {info.articlesDiff}</span>
            <span>Nombre d'article total : {info.nbTotalArticle}</span>
            <span>Sous-total : {info.sousTotal}</span>
            <span>TPS : {info.tps}</span>
            <span>TVQ : {info.tvq}</span>
            <span>Total : {info.total}</span>
        </div>
    )
}


/**
 * calcul les valeurs necessaire a l'affichage du sommaire du panier
 * 
 * @param {object} objetPanier Object contenant le detail du panier
 * 
 * @return {object} Objet contenant toutes les valeurs requises
 */
function calculInfoPanier(objetPanier){
//objet contenant les valeurs a calculer
 const objectInfo = {
     articlesDiff: 0,
     nbTotalArticle: 0,
     sousTotal: 0,
     tps: 0,
     tvq: 0,
     total:0,
 }

//extraire le tableau des valeurs de l'objet contenant le panier
const articles = Object.values(objetPanier);
//console.log("le tableau des articles dans le panier : ",article)

//nombre article differents
objectInfo.articlesDiff = articles.length;

//nombre article total
objectInfo.nbTotalArticle = articles.reduce((accumulateur, chaqueArticle) => accumulateur + chaqueArticle.qte, 0);

//calcul sous-total
objectInfo.sousTotal = articles.reduce((accumulateur, chaqueArticle) => accumulateur + (chaqueArticle.qte * chaqueArticle.prix), 0).toFixed(2);

//TPS
objectInfo.tps = (objectInfo.sousTotal * 0.05).toFixed(2);

//TVQ
objectInfo.tvq = (objectInfo.sousTotal * 0.1).toFixed(2);

//total
// calculer avec - 0 reconvertit les string (toFixed convertit en string) en nombre, on aurais pus faire * 1
objectInfo.total = ((objectInfo.sousTotal - 0) + (objectInfo.tps - 0) + (objectInfo.tvq - 0)).toFixed(2);

return objectInfo;
}