import './Produit.scss';
import BtnAjoutPanier from './BtnAjoutPanier';

export default function Produit(props){

    const [panier, setPanier] = props.etatPanier;

    function ajouterAuPanier() {
        const article = panier[props.id]; //si lobjet (id) est deja dans le panier, ajoute seulement la quantite
        if(article){
            article.qte++;
        }
        else{
            panier[props.id] = {prix: props.prix, qte:1};
        }
        console.log(panier);

        //tant quon a pas fait appel a setPanier, react ne sait pas que l'etat du panier est different
        let clonePanier = {...panier};
        /*exemple2 non utilise // let ClonePanier2 = JSON.parse(JSON.stringify(panier)); */
        /*exemple3 non utilise // let ClonePanier3 = Object.assign({}, panier);//donne un objet vide {} et assigne cet objet au panier */

        setPanier(clonePanier);
    }
    
    let qteArticlePanier = 0;
    let txtBtnAjout = "Ajouter au panier";
    let btnCouleurAjout ="";
    if(panier[props.id]){
        qteArticlePanier = panier[props.id].qte;
        txtBtnAjout = "Quantité augmenté";
        btnCouleurAjout = "rgb(235, 65, 94)";
    }
    
    return(
        <>
        <ul className="LeProduit">
            <li>
                <img src={'ImageProduits/'+ props.id + '.webp'} alt={props.nom}/>
                <div className="info">
                    <p className="nom">{props.nom}</p>
                    <p className="prix">{props.prix}</p>
                </div>
                <BtnAjoutPanier onClick={ajouterAuPanier} qte={qteArticlePanier} texte={txtBtnAjout} couleur={btnCouleurAjout}/>
            </li>
        </ul>
        </>
    );
}